import React from 'react';
import './App.css';
import Sidebar from './components/sidebar';
import  './components/navbar.css';
import State from './components/state';
import Active from './components/active';
import Recovered from './components/recovered';
import Confirmed from './components/confirmed';
import Deceased from './components/deceased';
import Piediagram from './components/piediagram';
import Rate from './components/rate';
import Bargraph from './components/bargraph';
import Guidelines from './components/guidelines';
import Higheststate from './components/higheststate';
import Photo from './photo.jpg';
class App extends React.Component {
  state = { 
    statewiseData:[],
    indialiveData:0,
    indialiveDataLength:'',
    statename:'India',
    states1:['India'],
    states:[],
    higheststate:[],
    highestconfirmed:[],
    date:'',
    dateandtime:'',
    active:'',
    confirmed:'',
    recovered:'',
    deceased:'',
    value:0,
    recoveryRate:55,
    mortalityRate:4,
    piedata: [],
    pieoptions:{
      cutoutPercentage:60, 
      maintainAspectRatio: false,
      legend: {
        display: false
      }
    },
    dailyconfirmed:[],
    dailydeceased:[],
    dailyrecovered:[],
    bardata:[],
    barlabels:[],
    barlabel:'Confirmed Cases',
    barcolor:'rgb(223, 105, 105)',
    display:'box-display'
}

   componentDidMount = () => {
    fetch('https://api.covid19india.org/data.json').then(res => res.json()).then((data) => {
      this.setState({
             statewiseData:data.statewise,
             indialiveData:data.cases_time_series,
             date:data.cases_time_series[data.cases_time_series.length-1].date,
             dateandtime:data.statewise[0].lastupdatedtime,
             active:data.statewise[0].active,
             confirmed:data.statewise[0].confirmed,
             recovered:data.statewise[0].recovered,
             deceased:data.statewise[0].deaths,
             recoveryRate:Math.round(data.statewise[0].recovered/data.statewise[0].confirmed*100),
             mortalityRate:Math.round(data.statewise[0].deaths/data.statewise[0].confirmed*100),
             piedata:[data.statewise[0].active,data.statewise[0].recovered,data.statewise[0].deaths] ,
             indialiveDataLength:data.cases_time_series.length},() =>{
             for(let i = this.state.indialiveData.length-1 ; i>=this.state.indialiveData.length-5 ; i--){
             this.state.dailyconfirmed.push(this.state.indialiveData[i].dailyconfirmed/1000)
             this.state.dailyrecovered.push(this.state.indialiveData[i].dailyrecovered/1000)
             this.state.dailydeceased.push(this.state.indialiveData[i].dailydeceased/1000)
             this.state.barlabels.push(this.state.indialiveData[i].date)
             }
             this.state.dailyconfirmed.reverse();
             this.state.dailyrecovered.reverse();
             this.state.dailydeceased.reverse();
             this.state.barlabels.reverse();
             for(let i = 1 ; i < this.state.statewiseData.length ;i++){
             this.state.states1.push(this.state.statewiseData[i].state)
             if(i>=1 && i<=5){
               this.state.higheststate.push(this.state.statewiseData[i].state)
               this.state.highestconfirmed.push(this.state.statewiseData[i].confirmed)
             }
             }
             })
            this.setState({states:this.state.states1,bardata:this.state.dailyconfirmed})
        })
    }



   handleChange = (event) =>{
    this.setState({
       value:event.target.value,
       active:this.state.statewiseData[event.target.value].active,
       confirmed:this.state.statewiseData[event.target.value].confirmed,
       recovered:this.state.statewiseData[event.target.value].recovered,
       deceased:this.state.statewiseData[event.target.value].deaths,
       recoveryRate:Math.round(this.state.statewiseData[event.target.value].recovered/this.state.statewiseData[event.target.value].confirmed*100),
       mortalityRate:Math.round(this.state.statewiseData[event.target.value].deaths/this.state.statewiseData[event.target.value].confirmed*100),
       statename:this.state.states[event.target.value],
       piedata:[this.state.statewiseData[event.target.value].active,this.state.statewiseData[event.target.value].recovered,this.state.statewiseData[event.target.value].deaths]
       })
   }
   toggleDot = () =>{
     this.setState({display:'dialog-3-box'})
   }
   exitBox = () => {
    this.setState({display:'box-display'})
   }
   getConfirmed = () => {
     this.setState({
      bardata:this.state.dailyconfirmed,
      barlabel:'Confirmed Cases',
      barcolor:'rgb(223, 105, 105)',
     })
   }
   getRecovered = () => {
    this.setState({
     bardata:this.state.dailyrecovered,
     barlabel:'Recovered Cases',
     barcolor:'rgb(103, 250, 44)',
    })
  }
  getDeceased = () => {
    this.setState({
     bardata:this.state.dailydeceased,
     barlabel:'Deceased Cases',
     barcolor:'rgb(248, 195, 47)',
    })
  }
  render() { 
    return ( 
      <div className="d-flex">
        <div><Sidebar /></div>
        <div className="content">
         <div>
            <nav class="navbar navbar-light">
				        <div>
                    <select value={this.state.value} placeholder="Search" className="state-name" onInput={this.handleChange}>
                         {this.state.states.map((item,index)=> <option value={index}>{item}</option>)}
                    </select>
                </div>
                <div className="d-flex deep-margin">
                        <div><img src={Photo} width="35" style={{borderRadius:'50px'}}></img></div>
                        <div className="deep-name">
                            <h5 style={{color:'#fff'}}>Deep Mandlik</h5>
                            <p style={{color:"grey"}}>Founder</p>
                        </div>
                </div>  
             </nav>
         </div>
         <div className="container-fluid scroll pad">
         <State dateandtime={this.state.dateandtime} statename={this.state.statename}/>
         <div className="row">
				  <div className="col-lg-5">
					<div className="row">
						<Active date={this.state.date} active={this.state.active}/>
					  <Confirmed date={this.state.date} confirmed={this.state.confirmed}/>
					</div>
					<div className="row">
					  <Recovered date={this.state.date} recovered={this.state.recovered}/>
						<Deceased date={this.state.date} deceased={this.state.deceased}/>
					</div>
          <div className="row">
            <Rate recoveryRate={this.state.recoveryRate} mortalityRate={this.state.mortalityRate}/>
          </div>
				  </div>
				  <div className="col-lg-7">
          <Piediagram piedata={this.state.piedata} active={this.state.active} recovered={this.state.recovered} deceased={this.state.deceased} options={this.state.pieoptions} />
           </div>
				</div>
        <div className="row"><div className="col-lg-12"><h4 style={{color:'#fff'}} className="mb-2">India Live Update</h4></div></div>
        <div className="row">
        <Bargraph getDeceased={this.getDeceased} getRecovered={this.getRecovered} getConfirmed={this.getConfirmed} exitBox={this.exitBox} toggleDot={this.toggleDot} display={this.state.display} bardata={this.state.bardata} barlabels={this.state.barlabels} barlabel={this.state.barlabel} barcolor={this.state.barcolor} />
        <Higheststate higheststate={this.state.higheststate} highestconfirmed={this.state.highestconfirmed} />
        <Guidelines />
        </div>
         </div>
        </div>

      </div>
      );
  }
}
 
export default App;
